#namestarts {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 1rem;
    max-width: calc(100% - 40px);
    overflow: hidden;
}

#topdiv {
    align-self: flex-start;
    display: flex;
    flex-direction: row;
    align-items: center;
}
#topdivimg {
    height: 44px !important;
    width: 44px !important;
}

#namestarts > p {
    font-size: 14px;
    font-weight: 700;
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-align: left;
    margin: 0px;
}

#topdiv p:last-child {
    margin: 0 0;
    display: flex;
    flex-direction: row;
    align-items: center;
}

#slideparent {
    background-color: transparent;
    display:flex;
    flex-direction: column;
    /* align-items: center; */
    justify-content:center;
    /* position: absolute;
    top: 3rem;
    left: 1rem; */
    color: black;
}

.awssld__content {
    background-color: transparent;
    padding-inline: 1.5rem;
    justify-content: start;
}
@media (min-width: 320px) {
    .awssld__content {
        padding-inline: 0rem;
    }
}
@media (min-width: 521px) {
    .awssld__content {
        padding-inline: 8vw;
    }
    .awssld__prev {
        left: -0.25rem;
    }
    .awssld__next {
        right: -0.25rem;
    }
}

#readmore {
    font-size: 14px;
    color: rgba(17, 17, 17, 0.5);
    margin-top: 0.25rem;
}
#readmore:hover {
    text-decoration: underline;
}

#reviewtext {
    width: 100% !important;
    text-align: left;
    margin-left: 0px !important;
    margin-top: 8px !important;
    line-height: 1.4;
    color: rgb(17, 17, 17);
    font-size: 16px;
}

#timedisplay {
    overflow: hidden;
    min-width: 0px;
    font-weight: 400;
    font-size: 12px;
    line-height: 1.17;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: rgba(17, 17, 17, 0.5);
}
.awssld--active {
    /* height: 100% !important; */
}

#slidestars {
    margin-right: 0.5rem;
    margin-top: 0.33rem;
}

#starsnname {
    display: flex;
    flex-direction: row;
    align-items: center;
    max-width: 100%;
}
#reviewtextbox {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 100%;
}
.awssld__controls {
    margin-top: 1rem;
}
.gaUYjb {
    display: block !important;
    position: static !important;
    color: inherit !important;
    max-width: 100% !important;
    text-decoration: none !important;
    border: none !important;
    margin-top: 12px;
}
.eCIWIG {
    text-align: left;
}